/* =Header
-------------------------------------------------------------- */

.header {
  height: 100px;
  width: 100%;
  background: #fff;
  z-index: 9;
  position: relative;
}

.logo {
  float: left;
  line-height: 100px;
  h1 {
    line-height: 100px;
  }
}

a {
  >span.site-name {
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: bold;
    font-size: 16px;
  }
  &:hover {
    >span.site-name {
      color: #333;
    }
  }
}